// 左侧导航组件的样式
.left-nav {
  .left-nav-header {
    height: 80px;
    display: flex;
    align-items: center;
    background-color: #002140;
    img {
      margin: 0 15px;
      width: 40px;
      height: 40px;
    }
    h1 {
      color: #fff;
      font-size: 20px;
      margin: 0;
    }
  }
}

@primary-color: #2daebf;